import React, {useState, useEffect} from 'react';
import './item.css';
import Button from "./Button";

const ingredient = ["Cheese", "Lettuce", "Fried-egg", "Tomato"]
const meat = ["Pork Sausage", "Fried-Fish", "Grilled-Chicken"]


function ButtonList(props) {
    const [activeMeat, setActiveMeat] = useState("")
    const [setOfIng, setActiveIng] = useState([])
    const [summary, setSummary] = useState(new Set("Lettuce"))



    const handleMeat = (e) => {
        setActiveMeat(e.target.value)
    }

    const addIng = (ing) => {
        if (setOfIng) {
            const totalIng = setOfIng.push(ing)
            setActiveIng(totalIng)
            // todoRef.push(totalIng)

            // setTask("")
        }
    }

    const handleSubmit = () => {
        setSummary({"meat": activeMeat, "ingredient": setActiveIng})
        (summary);
    }

    const handleChange = (e) => {

    }

    return (
        <div className="card" style={{width: "800px", margin: "30px auto", padding: "10px"}}>
            {meat.map(m =>
                <Button onClick={(e) => handleMeat(e)} value={m} style={{margin: "5px"}} className={activeMeat === m ? "waves-effect btn-large amber darken-4" : "waves-effect btn-large amber darken-1"}>{m}</Button>
               )}


            <div className="card-content">
                <div>{ingredient.map(ing => <span className="item-list">{ing}
                    <button style={{margin: "5px"}} onClick={(e) => handleMeat(e)}
                       value={ing}
                       className={setOfIng.indexOf(ing) !== -1 ? "btn-floating btn-small waves-effect waves-light red" : "btn-floating btn-small waves-effect waves-light grey darken-3"}>
                        <i className="material-icons">add</i>
                    </button></span>)}
                </div>

                <div className="card">
                    <div style={{margin: "20px"}} className="input-field col s6">
                        <i className="material-icons prefix">mode_edit</i>
                        <textarea onChange={(e) => handleChange(e)} id="icon_prefix2"
                                  className="materialize-textarea"></textarea>
                        <label htmlFor="icon_prefix2">Customize your burger</label>
                    </div>
                </div>
                <button onClick={(e) => handleSubmit(e)} className="btn waves-effect waves-light" type="submit" name="action">Add
                </button>
            </div>


        </div>
    );
}


export default ButtonList;